body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {
    html, html:-webkit-full-screen, html:-webkit-full-screen, html:-moz-full-screen {
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg);
      -webkit-transform-origin: left top;
              transform-origin: left top;
      width: 100vh;
      overflow-x: hidden;
      position: absolute;
      top: 100%;
      left: 0;
    }
    html, html:fullscreen, html:-webkit-full-screen, html:-moz-full-screen {
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg);
      -webkit-transform-origin: left top;
              transform-origin: left top;
      width: 100vh;
      overflow-x: hidden;
      position: absolute;
      top: 100%;
      left: 0;
    }

    body, body:-webkit-full-screen, body:-webkit-full-screen, body:-moz-full-screen {
      background-color: white;
      touch-action: manipulation;
    }

    body, body:fullscreen, body:-webkit-full-screen, body:-moz-full-screen {
      background-color: white;
      touch-action: manipulation;
    }
}

#Game {
  background: url(data:image/png;base64,) repeat;
}
@media only screen and (min-width: 768px)  {
    #Title {
        font-family: 'Tetris';
        font-size: 180px;
    }
    
    #StartButton {
        font-size: 50px;
        color: white;
        padding-right: 45px;
        padding-left: 45px;
    }

    #Start {
        padding: 0px;
        width: 100vw;
        height: 100vh;
        background: white;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    #Game {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100vh;
        width: 100vw;
    }
    
    #Matrix {
        display: grid;
        grid-template-columns: repeat(10, 25px);
        grid-template-rows: repeat(15, 25px);
        padding: 2px;
        background-color: white;
        border: 20px solid #333;
        border-radius: 5px;
    }
    
    .cell {
        background-color: black;
        /*margin: 1px;*/
        /*border-radius: 4px;*/
        width: 25px;
        height: 25px;
    }

    #left-panel {
        margin-right: 50px;
    }

    #left-panel > div {
        border: 10px solid black;
    }

    #social-buttons {
        font-size: 45px;
        background-color: white;
        border: 5px solid black;
        margin-top: 30px;
        border-radius: 5px;
        text-align: center;
        padding: 5px;
    }
    
    #social-buttons > a {
        padding: 20px;
    }

    #right-panel {
        margin-left: 50px;
    }

    #right-panel > div > div {
        border: 10px solid black;
    }

    #MobileControls {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }

    #MobileControls > div > button {
        border-radius: 5px !important;
        width: 75px !important;
        margin: 2px !important;
        touch-action: manipulation  !important;
        font-size: 50px !important;
        border: 2px solid black
    }
}

@media only screen and (max-width: 767px)  {
    #Title {
        font-family: 'Tetris';
        font-size: 120px;
    }
    
    #StartButton {
        font-size: 20px;
        color: white;
        padding-right: 45px;
        padding-left: 45px;
    }

    #Start {
        padding: 0px;
