        justify-content: center;
        align-items: center;
    }

    #Game {
        padding: 0px;
        width: 100vw;
        height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    #Game > div {
        padding: 0px;
    }
    
    #Matrix {
        display: grid;
        grid-template-columns: repeat(10, 25px);
        grid-template-rows: repeat(15, 25px);
        padding: 2px;
        background-color: grey;
        border: 10px solid #333;
    }
    
    .cell {
        background-color: black;
        /*margin: 1px;*/
        /*border-radius: 4px;*/
        width: 25px;
        height: 25px;
    }
    
    #Panel {
        display: flex;
        padding: 0px;
        margin-bottom: 2px;
    }

    #Panel > div {
        padding: 0px;
    }
    
    #left-panel {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }

    #social-buttons {
        font-size: 35px;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        background-color: white;
        border: 2px solid black;
        border-radius: 5px;
        text-align: center;
        padding: 3px;
        margin: 2px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
    
    #social-buttons > a {
        padding-left: 15px;
        padding-right: 15px;
    }

    #MobileControls {
        display: flex;
        justify-content: center;
        font-size: 30px !important;
    }

    #MobileControls > div > button {
        border-radius: 5px !important;
        width: 55px !important;
        margin: 2px !important;
        touch-action: manipulation  !important;
        font-size: 28px !important;
        border: 2px solid black
    }
}

@media only screen and (max-height: 679px)  {
    #social-buttons {
        display: none
    }
}

.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

